<template>
  <div class="noticeData" v-if="zixunData">
    <p class="title">{{zixunData.title}}</p>
    <p class="time">{{$public.timestampToTime(zixunData.create_time)}}</p>
    <div class="center" v-html="zixunData.content"></div>
  </div>
</template>
<style lang='less'>
@import "../../../assets/index.less";
@import "noticeData.less";
</style>
<script>
import { zixun_detail } from "../../../api/c2c";
export default {
  data() {
    return {
      zixunData: null
    };
  },
  created() {
    var _this = this;
    zixun_detail({ id: this.$route.query.id }).then(res => {
      switch (res.status == 200) {
        case true:
          _this.zixunData = res.data.data;
          // console.log(_this.zixunData);
          break;
        default:
          _this.$public.msg(res.data.msg, "error", _this);
          break;
      }
    });
  }
};
</script>



